.scs-slider {
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: var(--scs-overscroll-behavior, auto);
  scroll-behavior: var(--scs-scroll-behavior, smooth);
  scroll-snap-type: var(--scs-snap-type, x mandatory);
  scrollbar-width: none;
  touch-action: var(--scs-touch-action, pan-x);
  width: 100%;
