  margin-right: var(--scs-gap, 8px);
}

@media (width >= 512px) {
  .scs-slide {
    flex-basis: var(--scs-min-w-512, var(--scs-min-w, 100%));
    min-width: var(--scs-min-w-512, var(--scs-min-w, 100%));
    width: var(--scs-min-w-512, var(--scs-min-w, 100%));
  }
