.scs-nav {
  appearance: none;
  background-color: var(--scs-arrow-bg, #fff);
  border-radius: var(--scs-arrow-radius, 4px);
  cursor: pointer;
  padding: var(--scs-arrow-padding, .5em);
  user-select: none;
  z-index: 1;
  border: 0;
  outline: none;
  place-items: center;
  line-height: 0;
  transition: all .1s ease-in-out;
  display: grid;
  position: absolute;
}

.scs-nav:disabled {
